import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core'
import { SwiperComponent as OrigSwiperComponent } from 'swiper/angular/angular/src/swiper.component'
import { Product } from '@core/models/product.model'
import { NavigationOptions } from 'swiper/types'
import SwiperCore, { FreeMode, Navigation } from 'swiper'
import { ProductListingType } from '@core/enums/product-listing-type.enum'

SwiperCore.use([FreeMode, Navigation])

@Component({
  selector: 'app-product-swiper',
  templateUrl: './product-swiper.component.html',
  styleUrls: ['./product-swiper.component.scss']
})
export class ProductSwiperComponent {

  @ViewChild('swiper', { static: false }) swiper: OrigSwiperComponent

  @Input()
  public products: Array<Product>

  @Input()
  public icon: string

  @Input()
  public title: string

  @Output()
  public addToCart = new EventEmitter<any>()

  public navigationName = 'swiper-product'
  public navOpt: NavigationOptions = { nextEl: `.${this.navigationName}-next`, prevEl: `.${this.navigationName}-prev` }
  public viewTypeGrid: ProductListingType = ProductListingType.grid

  constructor() { }

  ngOnInit(): void {
  }

  public _addToCart(product: Product) {
    this.addToCart.emit()
  }

}
